import {useRef} from 'react'
import React from 'react'
import Card from './ui/Card' 
import classes from './NewMeetupform.module.css'

function NewMeetupForm(props) {
const titleinputRef=useRef();
const imageinputRef=useRef();
const addressinputRef=useRef();
const descriptioninputRef=useRef();


  function submitHandler(event){
    event.preventDefault();

    const enteredTitle=titleinputRef.current.value;
    const enteredImage=imageinputRef.current.value;
    const enteredAddress=addressinputRef.current.value;
    const enteredDescription=descriptioninputRef.current.value;
    console.log(enteredTitle, enteredAddress,enteredDescription,enteredImage)

   const meetupdata={
     title:enteredTitle,
     image:enteredImage,
     address:enteredAddress,
     description:enteredDescription
   }
//  console.log(meetupdata )
props.onAddMeetup(meetupdata)
  }
  return (
<Card>
 <form className={classes.form} onSubmit={submitHandler}>
   <div className={classes.control}>
     <label htmlFor="title">Meetup Title</label>
     <input type="text" name="title" id="title" placeholder='Meetup title' required   ref={titleinputRef} />
     </div> 
     <div className={classes.control}>
     <label htmlFor="image">Image</label>
     <input type="url" name="image" id="image" placeholder='Image URL only' required ref={imageinputRef} />
     </div> 
     <div className={classes.control}>
     <label htmlFor="address">Address</label>
     <input type="text" name="address" id="address" placeholder='Address' required  ref={addressinputRef}/>
     </div> 
     <div className={classes.control}>
     <label htmlFor="description">Description</label>
     <textarea type="text" name="description" placeholder='Description ' rows='5' id="description" required  ref={descriptioninputRef} />
     </div> 
     <div className={classes.actions  }>
     <button type='submit'> Add meetup</button>
     </div> 
   </form>
</Card>
    
  )
}

export default NewMeetupForm;