import React from 'react'
// import {useNavigate} from 'react-router-dom';
import classes from './Meetupcard.module.css'
import Card from './ui/Card';
import {useContext} from 'react';
import FavoritesContext from '../store/favorites-context'

function Meetupcard(props) {
    // let navigate=useNavigate();
    // function ToFavorites(){
        
    //     navigate('./favorites');
    // }
const favoritesctx =useContext(FavoritesContext);
const itemFavorite=favoritesctx.itemFavorites(props.id);

    function toggeFavoritesHandler(){
        if(itemFavorite){
            favoritesctx.removeFavorites(props.id)
        }
        else{
            favoritesctx.addFavorites({
                id:props.id,
                title:props.title,
                description:props.description,
                image:props.img,
                address:props.address
            });
        }
    }
  return (
      <li className={classes.item }>
   
     <Card>
    <div className={classes.img }>
    <img className={classes.image } src={props.img} alt={props.title} />
    </div>
         
            <div className={classes.content }>
        <h1>{props.title}</h1>
        <address>{props.address}</address> 
        <p>{props.description}</p>
        </div>
         
         
         <div className={classes.actions }><button onClick={toggeFavoritesHandler} > {itemFavorite ? 'remove from favorites':'add to favorites' }</button></div>
         </Card>
         </li>
  )
}

export default Meetupcard; 