import React from 'react'
import Meetupcard from './Meetupcard'
import classes from './Meetupcard.module.css'

function Meetuplist(props) {
  return (
    <ul className={classes.list}>
        {props.meetups.map(
            meetup=><Meetupcard key={meetup.id} id={meetup.id} img={meetup.image } address={meetup.address } description={meetup.description} title={meetup.title}/>
        )}
        {/* <Meetupcard title={meetupitem.title} img={meetupitem.image} /> */}
    </ul>
  )
} 

export default Meetuplist